import Vue from 'vue'
import Router from 'vue-router'
/**
 *  
 */
import login from '@/components/Logon_process/login' // 
import NoMember from '@/components/Logon_process/NoMember' // 
import membershipApp from '@/components/Logon_process/membershipApp' // ,
import submitAdd from '@/components/Logon_process/submitAdd' // 
import applySuccess from '@/components/Logon_process/applySuccess' // 
import examineType from '@/components/Logon_process/examineType' // 
/**
 *  
 * homeContent      
 * International    
 * myCustomer       
 * my               
 * CuInfo           
 * FollowNumber     
 * workEx           
 * saleFootprint    
 */
import home from '@/components/Main_interface/home' //  NAV 
import CuHome from '@/components/Main_interface/CuHome' // 
import ApplyOpen from '@/components/Main_interface/ApplyOpen' // 
import cardInfo from '@/components/Main_interface/cardInfo' // 
import HisCustomer from '@/components/Main_interface/HisCustomer' // Ta
import HisFans from '@/components/Main_interface/HisFans' // Ta
import HisFollow from '@/components/Main_interface/HisFollow' // Ta
import MarkupCu from '@/components/Main_interface/MarkupCu' //  
import srceach from '@/components/Main_interface/srceach' // 
import SalesNotes from '@/components/Main_interface/SalesNotes' // 
import editComment from '@/components/Main_interface/editComment' // 
import myNiuzuan from '@/components/Main_interface/myNiuzuan' // 
import NiuzuanRecord from '@/components/Main_interface/NiuzuanRecord' // 
import myInvitation from '@/components/Main_interface/myInvitation' // 
import editInfo from '@/components/Main_interface/editInfo' //    
import batchMarking from '@/components/Main_interface/batchMarking' //    Excel
/**
 *  
 */
import nav from '@/components/Admin/nav' //  
import MemberList from '@/components/Admin/MemberList' //  
import newClub from '@/components/Admin/newClub' //  
import memberInfo from '@/components/Admin/memberInfo' //  
import memberJournal from '@/components/Admin/memberJournal' //  
import CustomerInfo from '@/components/Admin/CustomerInfo' //  
import memberCustomer from '@/components/Admin/memberCustomer' //  

Vue.use(Router)

export default new Router({
  // mode: 'history',
  mode: 'hash',
  routes: [
    { path: '/login', name: 'login', component: login },
    { path: '/NoMember', name: 'NoMember', component: NoMember },
    { path: '/membershipApp', name: 'membershipApp', component: membershipApp },
    { path: '/submitAdd', name: 'submitAdd', component: submitAdd },
    { path: '/applySuccess', name: 'applySuccess', component: applySuccess },
    { path: '/examineType', name: 'examineType', component: examineType },
    /**
     * ------------------------------- Main_interface 
     */
    { path: '/home', name: 'home', component: home },
    { path: '/CuHome', name: 'CuHome', component: CuHome },
    { path: '/ApplyOpen', name: 'ApplyOpen', component: ApplyOpen },
    { path: '/cardInfo', name: 'cardInfo', component: cardInfo },
    { path: '/HisCustomer', name: 'HisCustomer', component: HisCustomer },
    { path: '/HisFans', name: 'HisFans', component: HisFans },
    { path: '/HisFollow', name: 'HisFollow', component: HisFollow },
    { path: '/MarkupCu', name: 'MarkupCu', component: MarkupCu },
    { path: '/srceach', name: 'srceach', component: srceach },
    { path: '/SalesNotes', name: 'SalesNotes', component: SalesNotes },
    { path: '/editComment', name: 'editComment', component: editComment },
    { path: '/myNiuzuan', name: 'myNiuzuan', component: myNiuzuan },
    { path: '/NiuzuanRecord', name: 'NiuzuanRecord', component: NiuzuanRecord },
    { path: '/myInvitation', name: 'myInvitation', component: myInvitation },
    { path: '/editInfo', name: 'editInfo', component: editInfo },
    { path: '/batchMarking', name: 'batchMarking', component: batchMarking },
    /**
     * ------------------------------- Main_interface 
     */
    { path: '/nav', name: 'nav', component: nav },
    { path: '/MemberList', name: 'MemberList', component: MemberList },
    { path: '/newClub', name: 'newClub', component: newClub },
    { path: '/memberInfo', name: 'memberInfo', component: memberInfo },
    { path: '/memberJournal', name: 'memberJournal', component: memberJournal },
    { path: '/CustomerInfo', name: 'CustomerInfo', component: CustomerInfo },
    { path: '/memberCustomer', name: 'memberCustomer', component: memberCustomer }
  ]
})



