export default {
  name: 'CuInfo',
  data () {
    return {
      tapBur: true,
      msg: {}
    }
  },
  methods: {
    mol: function (num) {
      this.$emit('mol', num)
    },
    cardInfoInit: function (data) {
      let _this = this
      _this.api.getUserCard(data, function (res) {
        console.log(res)
        _this.msg = res.data
      }, function (err) {
        console.log(err)
      })
    },
    cuHomeInit: function (id) {
      let _this = this
      let str = 'company=' + id
      _this.api.getCompanyInfo(str, function (res) {
        console.log(res)
        _this.msg = res.data
        _this.msg.contact_list = JSON.parse(res.data.contact_list)
        console.log(_this.msg)
      }, function (err) {
        console.log(err)
      })
    },
    SalesNotesInit: function (id) {
      let _this = this
      _this.api.getMyCustomers(id, function (res) {
        console.log(res)
        _this.msg = res.data
        _this.msg.contact_list = JSON.parse(_this.msg.contact_list)
        console.log(_this.msg)
      }, function (err) {
        console.log(err)
      })
    }
  },
  mounted (options) {
